@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Century Gothic";
  font-display: swap;
  src: url("../fonts/centurygothic.woff2") format("woff2"), url("../fonts/centurygothic.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Century Gothic";
  font-display: swap;
  src: url("../fonts/centurygothic-bold.woff2") format("woff2"), url("../fonts/centurygothic-bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  font-family: "Century Gothic", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.container {
  padding-right: calc((100% - 1220px) / 2);
  padding-left: calc((100% - 1220px) / 2); }
  @media (max-width: 1280px) {
    .container {
      padding-left: 50px;
      padding-right: 50px; } }
  @media (max-width: 768px) {
    .container {
      padding-left: 25px;
      padding-right: 25px; } }

